import { graphql, useStaticQuery } from "gatsby";

export const useSiteMetadata = () => {
  const { site } = useStaticQuery<{
    site: {
      siteMetadata: {
        avatar?: string;
        author?: string;
        title?: string;
        description?: string;
        helpText?: string;
        user?: string;
        host?: string;
      };
    };
  }>(graphql`
    query SiteMetadata {
      site {
        siteMetadata {
          avatar
          author
          title
          description
          helpText
          user
          host
        }
      }
    }
  `);

  return site.siteMetadata;
};
